import React, { useState } from "react";
import Button from '@mui/material/Button';
import AddCircleIcon from '@mui/icons-material/AddCircle';


function CreateNote (props) {

   const [expand, setExpand] = useState(false);



    const [note, setNote] = useState({
      title:"",
      content:"",
    }); 


    const InputEvent = (event) => {

        // const value = event.target.value;
        // const name= event.target.name;

        const { name , value} = event.target;

        setNote((prevData) => {
            return {
                ...prevData,
                [name]: value,  //spread operator use for destructing
                

            };
        });
        console.log(note);
    };

    const addEvent =() => {

       props.passNote(note);
       setNote({
        title:"",
        content:"",
      }); 
    };
    

    const expandIt = () => {
        setExpand(true);   //to expand text area
    }

    const btoNormal = () => {
        setExpand(false);   //to shrink text area on double click
    }
    return (
        <>
        <div className="main_note" >
            <form>
                { expand?
                <input type="text"
                name="title"
                 value={note.title} 
                 onChange= {InputEvent}
                 placeholder="Title"
                  autoComplete="off" /> : null }

                <textarea rows="" column=""
                name="content"
                 value={note.content} 
                onChange= {InputEvent} 
                placeholder="Write a note..." 
                onClick ={expandIt}
               />
                
                 {expand? 
                 <Button onClick={addEvent}>
                     <AddCircleIcon className= "plus_sign" />
                     </Button> : null}
                 
                </form>
        </div>

        </>
        

    
      
    );
}


export default CreateNote;
 