import React, { useState } from 'react';
 import Header from './Header';
 import Footer from './Footer';
// import logo from './logo.svg';
import CreateNote from './CreateNote';
import Note from './Note';




function App() {
  const [addItem, setAddItem] = useState([]);


  const addNote =(note) => {
     //alert("i am click");
     setAddItem((prevData) => {
     return [...prevData, note]
     });
     console.log(note);
  
  };
  const onDelete=(id)=>{
    setAddItem((olddata) => 
    olddata.filter((currentdata, index) => {  //helps to filter out the notes which we have not deleted
      return index !== id;
    })
    );

  };





  return (
    <>
    <Header />

    
    <CreateNote
    passNote= {addNote} />
    

    {addItem.map((val,index) => {
      return( <Note 
      key={index}
      id={index}
      title={val.title}
      content = {val.content}
      deleteItem={onDelete}
      />)

    })}
    <Footer />

    </>
    
    
  );
}

export default App;
